import React, { useEffect, useRef } from 'react';
import { useAxios, useNotistack } from 'shared/hooks';
import { useParams } from 'react-router';
import { Helmet } from 'react-helmet';
import { storage } from '../../shared/services';

function EmailConfirmation() {
	const params = useParams();
	const { showNotification } = useNotistack();
	const stsRequest = useRef(false);

	//https://localhost:3002/app/sts/ODVhZGU5NGZjY0BjYXRkb2dtYWlsLmxpdmU?is_registering_new=true

	const { fetchData: handleRequest } = useAxios({
		url: '',
		dynamicUrl: true,
		cb: {
			success: (data, args) => {
				if (data.token) {
					storage.set('token', data.token);
				}
				window.location.href = '/';
			},
			error: (error, args) => {
				showNotification({
					message: error?.response?.data?.message || 'The request is incorrect',
					variant: 'error'
				});
			}
		}
	});

	useEffect(() => {
		if ('code' in params && !stsRequest.current) {
			stsRequest.current = true;
			//@ts-ignore
			handleRequest({ url: `/user/sts/${params.code}/` });
		}
	}, [params]);

	return (
		<div className={'h-full w-full p-8'}>
			<Helmet>
				<title>KENNEKT | Email confirmation</title>
			</Helmet>

			<span>Your request is being handling. You will be signed in after process.</span>
		</div>
	);
}

export default EmailConfirmation;
